import React from 'react'
import Logo from "./logo.png"
import "./TopMenu.css"

function TopMenu(props) {
    return (
        <div className="TopMenu">
            <ul>
                <li><img className="logo" src={Logo} alt="Agenda Tupã"></img></li>
                <li><button type="button" className="menubutton" onClick={props.botao1}>Home</button></li>
                <li><button type="button" className="menubutton" onClick={props.botao2}>Meus Eventos</button></li>
                <li><button type="button" className="menubutton">Conectar-se</button></li>
            </ul>
        </div>
    )
}

export default TopMenu
