import React, {useState} from 'react'
import "./MiniAtracao.css"
import ModalAtracao from './ModalAtracao/ModalAtracao'
import heartfull from "./heartfull.png"
import heartempty from "./heartempty.png"

export default function MiniAtracao(props) {
    const [modalHidden, setmodalHidden] = useState(true)
    const toogleModal=()=>{
        setmodalHidden(!modalHidden)
    }
    const [liked, setliked] = useState(false)
    return (
        <div className="AtracaoContainer">
            <img onClick={()=>toogleModal()} src={props.imgsrc||"./musica.png"} alt="atracao" className="Atracao"/>
            <p className="nome">{props.name||"Nome"}</p>
            <p className="date">{props.date||"Data"}</p>
            <p className="tipo">{props.type||"Tipo"}</p>
            {modalHidden?"":<ModalAtracao close={()=>toogleModal()} name={props.name} date={props.date} local="Casa do Joaquim" type={props.type}/>}
            <div className="likedContainer"><img className="liked" onClick={()=>setliked(!liked)} src={liked?heartfull:heartempty} alt="Liked?"/></div>
        </div>
    )
}
