import React from 'react'
import "./ModalAtracao.css"
import XIcon from "./xIcon.jpg"

function ModalAtracao(props) {
    return (
        <div>
            <div className="ModalAtracao">
                <button onClick={props.close} className="closeButton"><img src={XIcon} alt="Fechar" style={{width:"64px"}}></img></button>
                <h2>{props.name||"Nome"}</h2>
                {props.type?<p>{props.type}</p>:""}
                <p>Data: {props.date||"Data"}</p>
                <p>Inscrições: {props.inscricoes||"Livre"}</p>
                <p>Local: {props.local||"Local"}</p>
                <div dangerouslySetInnerHTML={{__html: '<iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3701.2476219790674!2d-50.52396168445674!3d-21.925030009088488!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x0!2zMjHCsDU1JzMwLjEiUyA1MMKwMzEnMTguNCJX!5e0!3m2!1spt-BR!2sbr!4v1594229875211!5m2!1spt-BR!2sbr" width="400" height="300" frameborder="0" style="border:0;" allowfullscreen="" aria-hidden="false" tabindex="0"></iframe>'}}/>
            </div>
            <div className="maskAtracao" onClick={props.close}></div>
        </div>

    )
}

export default ModalAtracao
