import React, {useState, useEffect} from 'react'
import "./GridAtracoes.css"
import MiniAtracao from "../MiniAtracao/MiniAtracao"
import FilterBar from '../FilterBar/FilterBar'

export default function GridAtracoes() {
    const [eventos,setEventos]= useState({
        evento:
        []
    })
    const [filter,setFilter]=useState("any");
    const [order,setOrder]=useState(()=>(a,b)=>a.date>b.date?1:-1);
    const [criteria,setcriteria]=useState("date")
    const orderBy=()=>{
        if (criteria==="date"){
            setcriteria("name")
            setOrder(()=>(a,b)=>a.name>b.name?1:-1);
        }else{
            setcriteria("date")
            setOrder(()=>(a,b)=>a.date>b.date?1:-1);
        }
    }
    useEffect(()=>{
        async function fetchdata(){
            const response = await fetch("./eventos.json")
            const result = await response.json()
            setEventos(result)
        };
        fetchdata()
    },[]
    )
    const detNEventos=()=>{
        return window.innerWidth/237}
    const [neventos, setneventos] = useState(detNEventos())
    useEffect(() => {
        window.addEventListener('resize', () => {
            setneventos(detNEventos())
       })
      },[])
    return (
        <div style={{width:"100%"}}>
            <FilterBar filter={(type)=>setFilter(type)} order={orderBy} criteria={criteria}/>
            <div className="grid">
                {eventos.evento
                .filter(evento=>evento.type===filter||filter==="any")
                .slice(0,Math.min(neventos,eventos.evento.length))
                .sort(order)
                .map((evento,key)=>{
                    return <div key={key} className="item"><MiniAtracao name={evento.name} date={evento.date} type={evento.type} imgsrc={evento.imgurl}/></div>
                })}
            </div>
        </div>
    )
}

