import React, {useState} from 'react'
import filterIcon from "./FilterIcon.png"
import sortIcon from "./SortIcon.png"
import "./FilterBar.css"
import Modal from "./ModalFilter/Modal.js"

export default function FilterBar(props) {
    //
    const [hidden, sethidden] = useState({display:"none"})
    return (
        <div className="bar">
            <button type="button" onClick={()=>sethidden({display:"block"})} className="barItem"><img src={filterIcon} alt="" style={{width:"32px"}}/>Filtrar</button>
    <button type="button" onClick={()=>props.order()} className="barItem"><img src={sortIcon} alt="" style={{width:"32px"}}/>Ordenar:{props.criteria==="name"?"Nome":"Data"}</button>
            <div style={hidden}>
                <Modal closeModal={()=>sethidden({display:"none"})} setFilter={(word)=>props.filter(word)}/>
            </div>
        </div>
    )
}
