import React, {useState} from 'react'
import "./AddAtracao.css"
import XIcon from "./xIcon.jpg"

function AddAtracao(props) {
    const [name, setname] = useState("Nome Atração")
    const [local, setlocal] = useState("Local")
    const [tipo, settipo] = useState("Tipo")
    const [inscricao, setinscricao] = useState("Valor inscrição")
    const [horario, sethorario] = useState("Horario")
    return (
        <div>
            <div className="AddAtracao">
                <button onClick={props.close} className="closeButton"><img src={XIcon} alt="Fechar" style={{width:"64px"}}></img></button>
                <form style={{margin:"20px"}}>
                    <label>
                        Nome: <br/>
                        <input type="text" value={name} onChange={e=>setname(e.target.value)}/>
                        <br/>
                    </label>
                    <label>
                        Tipo: <br/>
                        <input type="text" value={tipo} onChange={e=>settipo(e.target.value)}/>
                        <br/>
                    </label>
                    <label>
                        Horário: <br/>
                        <input type="text" value={horario} onChange={e=>sethorario(e.target.value)}/>
                        <br/>
                    </label>
                    <label>
                        Inscrições: <br/>
                        <input type="text" value={inscricao} onChange={e=>setinscricao(e.target.value)}/>
                        <br/>
                    </label>
                    <label>
                        Local: <br/>
                        <input type="text" value={local} onChange={e=>setlocal(e.target.value)}/>
                        <br/>
                    </label>
                    <button type="submit">Adicionar eventos</button>
                </form>
            </div>
            <div className="maskAtracao" onClick={props.close}></div>
        </div>

    )
}

export default AddAtracao
