import React, {useState} from 'react';
import './App.css';
import Banner from "./components/Banner/Banner" 
import GridAtracoes from "./components/GridAtracoes/GridAtracoes"
import TopMenu from './components/TopMenu/TopMenu';
import MyAtracoes from './components/MyAtracoes/MyAtracoes';
//import AddAtracao from './components/AddAtracao/AddAtracao';
//<AddAtracao/>
function App() {
  const [screen, setscreen] = useState("home")
  const setHome=()=>setscreen("home")
  const setMyapps=()=>setscreen("myapps")
  return (
    <div className="App">
      <header className="App-header">
        <TopMenu botao1={setHome} botao2={setMyapps}/>
        {screen==="home"? <div style={{marginTop:"70px"}}>
            <Banner></Banner>
            <GridAtracoes/>
          </div>:<MyAtracoes/>}
      </header>
    </div>
  );
}

export default App;
