import React, {useState, useEffect} from 'react'
import AddIcon from "./addIcon.svg"
import "./MyAtracoes.css"
import AddAtracao from "../AddAtracao/AddAtracao"

function MyAtracoes() {
    const [eventos,setEventos]= useState({
        evento:
        []
    })
    useEffect(()=>{
        async function fetchdata(){
            const response = await fetch("./eventos.json")
            const result = await response.json()
            setEventos(result)
        };
        fetchdata()
    },[]
    )
    const [close,setclose]= useState(true)
    const toogleClose=()=>{
        setclose(!close)
    }
    return (
        <div>
            <table className="list">
                <thead>
                    <tr>
                        <th>Nome</th>
                        <th>Data</th>
                        <th>Tipo</th>
                    </tr>
                </thead>
                <tbody>
                    {eventos.evento
                    .map((evento,key)=>{
                        return  <tr key={key} className="item">
                                    <td>{evento.name}</td>
                                    <td>{evento.date}</td>
                                    <td>{evento.type}</td>
                                </tr>
                    })}
                </tbody>
            </table>
            <img alt="Adicione Eventos" src={AddIcon} className="addIcon" onClick={toogleClose}></img>
            {close||<AddAtracao close={toogleClose}/>}
        </div>
    )
}

export default MyAtracoes
