import React, {useState} from 'react'
import "./Modal.css"

function Modal(props) {
    const [selectedOption, setselectedOption] = useState("any")
    return (
        <div className="screen">
            <div className="mask" onClick={props.closeModal}></div>
            <form className="Modal">
                <div className="radio">
                    <label>
                        <input type="radio" value="any" 
                        checked={selectedOption==="any"}
                        onChange={()=>setselectedOption("any")} />
                        Todos
                    </label>
                </div>
                <div className="radio">
                    <label>
                        <input type="radio" value="Música" 
                        checked={selectedOption==="Música"}
                        onChange={()=>setselectedOption("Música")} />
                        Música
                    </label>
                </div>
                <div className="radio">
                    <label>
                        <input type="radio" value="Teatro" 
                        checked={selectedOption==="Teatro"}
                        onChange={()=>setselectedOption("Teatro")} />
                        Teatro
                    </label>
                </div>
                <div className="radio">
                    <label>
                        <input type="radio" value="Dança" 
                        checked={selectedOption==="Dança"} 
                        onChange={()=>setselectedOption("Dança")} />
                        Dança
                    </label>
                </div>
                <div className="radio">
                    <label>
                        <input type="radio" value="Esporte"
                         checked={selectedOption==="Esporte"}
                         onChange={()=>setselectedOption("Esporte")}  />
                        Esporte
                    </label>
                </div>
                <button type="button" className="filterbutton" onClick={()=>{props.setFilter(selectedOption); props.closeModal()}}>Filtrar</button>
            </form>
        </div>
    )
}

export default Modal
